import React, { useState, useEffect } from "react";
import ArticleList from "../components/ArticleList";
import UpvotesSection from "../components/UpvotesSection";
import CommentsList from "../components/CommentsList";
import AddCommentForm from "../components/AddCommentForm";
import NotFoundPage from "../pages/NotFoundPage";
import articleContent from "../mock/articleContent";

const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = articleContent.find(article => article.name === name);
  const otherArticles = articleContent.filter(article => article.name !== name);

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
  }, [name]);

  if (!article) {
    return <NotFoundPage />;
  }

  return (
    <div>
      <h1 className="ui header">{article.title}</h1>
      {article.content.map((para, key) => (
        <p key={key}>{para}</p>
      ))}
      <div className="articleInfo">
        <div>
          <span>Source: </span>
          <a
            className="articleSourceLink"
            href={article.link}
            target="_blank"
            rel="noopener noreferrer"
          >
            {article.link}
          </a>
        </div>
        <UpvotesSection
          articleName={name}
          upvotes={articleInfo.upvotes}
          setArticleInfo={setArticleInfo}
        />
      </div>
      <CommentsList comments={articleInfo.comments} />
      <h3 className="header">Submit a new comment</h3>
      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />

      <h1 className="ui header">My Other Articles</h1>
      <ArticleList articles={otherArticles} layout="articleListHorizontal" />
    </div>
  );
};

export default ArticlePage;
