import React from 'react';
import ArticleList from '../components/ArticleList';
import articleContent from '../mock/articleContent';

const ArticlesListPage = () => {
  return (
    <div>
      <h1 className="ui header">My Atricles</h1>
      <ArticleList articles={articleContent} layout="articleListVertical"/>
    </div>
  );
}

export default ArticlesListPage;