import React, { useState } from "react";

const AddCommentForm = ({ articleName, setArticleInfo }) => {

  const [userName, setUserName] = useState('');
  const [commentText, setCommentText] = useState('');

  const addComment = async (e) => {
    e.preventDefault();
    const result = await fetch(`/api/articles/${articleName}/add-comment`, {
      method: 'post',
      body: JSON.stringify({ userName, text: commentText }),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    const body = await result.json();
    setArticleInfo(body);
    setUserName('');
    setCommentText('');
  }

  return (
    <form className="ui form">
      <div className="equal width fields">
        <div className="field">
          <label>Name:</label>
          <div className="ui fluid input">
            <input type="text" value={userName} onChange={(e) => setUserName(e.target.value)} placeholder="First name" />
          </div>
        </div>
      </div>
      <div className="field">
        <label>Comment:</label>
        <textarea value={commentText} onChange={(e) => setCommentText(e.target.value)} rows="3" />
      </div>
      <div className="field" onClick={(e) => addComment(e)}>
        <button type="button" className="ui button">Submit</button>
      </div>
    </form>
  );
};

export default AddCommentForm;
