import React from 'react';

const UpvotesSection = ({ articleName, upvotes, setArticleInfo }) => {
  
  const upvoteArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/upvote`, {
      method: 'post'
    });
    const body = await result.json();
    setArticleInfo(body);
  }

  return (
    <div className="ui right labeled button" role="button" tabindex="0">
      <button className="ui icon button" onClick={() => upvoteArticle()}>
        <i aria-hidden="true" className="heart icon"></i>
         Like
      </button>
      <p className="ui left pointing basic label">{upvotes}</p>
    </div>
  );
}

export default UpvotesSection;